import Game from "./js/Game";
import Level from "./js/Level";
import $ from "jquery";
import "./css/index.scss";
import "bootstrap/dist/css/bootstrap.min.css"

$("#gameStart").on("click", () => {
    if (Game.timer) {
        clearInterval(Game.timer);
        Game.gameTime = 20;
        console.log(111);
    }
    Level.creat();
    Level.level(3, "black", "yellow");
    Game.startGame();
});


Level.creat();
Level.level(3, "black", "yellow");
